import { StrictMode } from "react";
import ReactDOM from "react-dom";
import { createStore } from "redux";
import countManage from "./reducer";

import App from "./App";

const store = createStore(countManage);
const rootElement = document.getElementById("root");
ReactDOM.render(
  <StrictMode>
    <App store={store} />
  </StrictMode>,
  rootElement
);
